<template>
    <v-tooltip :color="color" bottom>
        <template v-slot:activator="{ on, attrs }">
            <v-icon :color="color" v-bind="attrs" v-on="on">{{ icon }}</v-icon>
        </template>
        <span>{{ message }}</span>
    </v-tooltip>
</template>

<script>
export default {
    name: 'BoolStatus',
    props: {
        status: {
            type: Boolean,
            required: true,
            default: false,
        },
        statusMessage: {
            type: String,
            required: false,
            default: 'Approved',
        },
    },
    computed: {
        icon() {
            return this.status ? 'mdi-checkbox-marked-circle' : 'mdi-close';
        },
        color() {
            return this.status ? 'success' : 'error';
        },
        message() {
            return this.statusMessage.length > 0
                ? this.statusMessage
                : this.status
                ? 'Approved'
                : 'Unknown Error';
        },
    },
};
</script>
