<template>
    <v-row class="justify-center">
        <v-col cols="12" md="10">
            <v-alert
                v-model="alert"
                :type="alertType"
                border="left"
                close-text="Close Alert"
                dismissible
                transition="scale-transition"
            >
                {{ alertMessage }}
            </v-alert>

            <v-dialog v-model="voidRefundDialog" width="550">
                <v-card>
                    <v-card-title class="text-h5 text-capitalize">
                        {{ voidOrRefund }} Options
                    </v-card-title>

                    <v-divider></v-divider>

                    <v-card-text class="mt-3">
                        Please select how you would like to
                        {{ voidOrRefund }} this sale
                    </v-card-text>

                    <v-card-actions>
                        <v-spacer></v-spacer>

                        <v-btn
                            color="error"
                            class="white--text text-capitalize"
                            @click="voidRefundTransaction"
                        >
                            {{ voidOrRefund }} Full Amount
                        </v-btn>

                        <v-btn
                            color="error"
                            class="white--text text-capitalize"
                            @click="
                                voidRefundPartialAmountDialog = true;
                                voidRefundDialog = false;
                                amount = '';
                            "
                        >
                            {{ voidOrRefund }} Partial Amount
                        </v-btn>

                        <v-btn
                            color="cyan lighten-1"
                            class="white--text text-capitalize"
                            @click="voidRefundDialog = false"
                        >
                            Cancel
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

            <v-dialog v-model="voidRefundPartialAmountDialog" width="550">
                <v-card>
                    <v-card-title class="text-h5">
                        Please enter {{ voidOrRefund }} amount
                    </v-card-title>

                    <v-divider></v-divider>

                    <v-col cols="12">
                        <v-text-field
                            label="Amount"
                            v-model="amount"
                            type="number"
                        ></v-text-field>
                    </v-col>

                    <v-card-actions>
                        <v-spacer></v-spacer>

                        <v-btn
                            color="grey"
                            text
                            class="text-capitalize"
                            @click="voidRefundPartialAmountDialog = false"
                        >
                            Cancel
                        </v-btn>

                        <v-btn
                            :color="getAppColors.primary"
                            class="white--text text-capitalize"
                            @click="voidRefundTransaction(false)"
                        >
                            OK
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

            <v-dialog v-model="captureDialog" width="550">
                <v-card>
                    <v-card-title class="text-subtitle-1">
                        Are you sure you want to capture this transaction?
                    </v-card-title>

                    <v-card-actions>
                        <v-spacer></v-spacer>

                        <v-btn
                            color="grey"
                            text
                            class="text-capitalize"
                            @click="captureDialog = false"
                        >
                            Cancel
                        </v-btn>

                        <v-btn
                            :color="getAppColors.primary"
                            class="white--text text-capitalize"
                            @click="capturePreauth()"
                        >
                            OK
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

            <v-dialog
                v-model="loadingActions"
                hide-overlay
                persistent
                transition="dialog-bottom-transition"
                width="300"
            >
                <v-card :color="getAppColors.primary" dark>
                    <v-card-text>
                        Processing...
                        <v-progress-linear
                            indeterminate
                            color="white"
                            class="mb-0"
                        ></v-progress-linear>
                    </v-card-text>
                </v-card>
            </v-dialog>

            <v-data-table
                :headers="headers"
                :items="transactions"
                :loading="loading"
                class="elevation-4 rounded-lg mt-2"
                loading-text="Loading... Please wait"
                :footer-props="tableFooterProps"
                sort-by="timestamp"
                sort-desc
                v-show="!loadingActions"
            >
                <template v-slot:[`item.timestamp`]="{ item }">
                    {{ formatDateTime(item.timestamp) }}
                </template>
                <template v-slot:[`item.sale_id`]="{ item }">
                    <template v-if="item.receipt_link != null">
                        <v-btn
                            class="white--text"
                            color="primary"
                            :href="item.receipt_link"
                            target="_blank"
                        >
                            {{ item.sale_id }}
                        </v-btn>
                    </template>
                    <template v-else-if="item.sale_id != null">
                        {{ item.sale_id }}
                    </template>
                    <template v-else> N/A </template>
                </template>
                <template v-slot:[`item.approved`]="{ item }">
                    <bool-status
                        :status="item.approved"
                        :status-message="item.responseDescription"
                    />
                </template>
                <template v-slot:[`item.authorizedAmount`]="{ item }">
                    <div
                        :class="
                            item.transactionType === 'void' ||
                            item.transactionType === 'refund' ||
                            item.transactionType === 'reverse'
                                ? 'red--text font-weight-bold'
                                : ''
                        "
                    >
                        {{
                            item.authorizedAmount
                                | currency(item.transactionType)
                        }}
                    </div>
                </template>
                <template v-slot:[`item.transactionType`]="{ item }">
                    {{ item.transactionType | titleCase }}
                </template>
                <template v-slot:[`item.entryMethod`]="{ item }">
                    {{ item.entryMethod | titleCase }}
                </template>
                <template v-slot:[`item.paymentType`]="{ item }">
                    {{ item.paymentType | titleCase }}
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                    <v-btn
                        v-if="
                            item.transactionType == 'charge' &&
                            item.approved &&
                            formatDate(item.timestamp) == formatDate(new Date())
                        "
                        color="error"
                        class="white--text text-none"
                        @click="
                            showVoidRefundDialog('void', item.transactionId)
                        "
                    >
                        Void
                    </v-btn>

                    <v-btn
                        v-else-if="
                            item.transactionType == 'charge' &&
                            item.approved &&
                            formatDate(item.timestamp) != formatDate(new Date())
                        "
                        color="error"
                        class="white--text text-none"
                        @click="
                            showVoidRefundDialog('refund', item.transactionId)
                        "
                    >
                        Refund
                    </v-btn>

                    <v-btn
                        v-else-if="
                            item.transactionType == 'preauth' && item.approved
                        "
                        :color="getAppColors.primary"
                        class="white--text text-none"
                        @click="
                            (captureDialog = true),
                                (selectedTransactionId = item.transactionId)
                        "
                    >
                        Capture
                    </v-btn>
                </template>
                <template v-slot:top>
                    <v-toolbar
                        color="green darken-2"
                        class="rounded-t-lg"
                        dark
                        flat
                    >
                        <v-toolbar-title>Transactions</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical />
                        <v-text-field
                            v-model="search"
                            label="Search"
                            append-icon="search"
                            single-line
                            hide-details
                            clearable
                            dark
                            @keyup.enter="
                                datepickerDateRange = [];
                                initialize();
                            "
                            @click:append="
                                datepickerDateRange = [];
                                initialize();
                            "
                            @click:clear="initialize()"
                        />
                        <v-spacer />
                        <v-toolbar-title>
                            Total Transactions for {{ dateRangeDisplay }}:
                            {{ getTotal | currency }}
                        </v-toolbar-title>
                        <v-divider class="mx-4" inset vertical />
                        <v-btn
                            color="primary"
                            rounded
                            dark
                            class="mr-2"
                            @click="excelExport"
                        >
                            <v-icon>mdi-download</v-icon>
                        </v-btn>
                        <v-menu
                            v-model="filterMenu"
                            :close-on-content-click="false"
                            offset-x
                            left
                            max-width="400px"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                    color="primary"
                                    rounded
                                    dark
                                    v-bind="attrs"
                                    v-on="on"
                                >
                                    <v-icon>mdi-filter</v-icon>
                                </v-btn>
                            </template>

                            <v-card>
                                <v-card-title> Filters</v-card-title>
                                <v-card-text>
                                    <v-row>
                                        <v-col cols="12">
                                            <v-select
                                                v-model="filterTypes"
                                                label="Transaction Types"
                                                :color="getAppColors.primary"
                                                item-color="green"
                                                append-icon="mdi-filter"
                                                :items="transactionTypes"
                                                multiple
                                                chips
                                                deletable-chips
                                                @change="saveFilterPreferences"
                                            />
                                        </v-col>
                                        <v-col cols="12">
                                            <v-menu
                                                v-model="datePickerMenu"
                                                :close-on-content-click="false"
                                                :nudge-right="40"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="auto"
                                                v-if="useCustomDateRange"
                                            >
                                                <template
                                                    v-slot:activator="{
                                                        on,
                                                        attrs,
                                                    }"
                                                >
                                                    <v-text-field
                                                        v-model="dateRangeText"
                                                        label="Choose Date Range"
                                                        :color="
                                                            getAppColors.primary
                                                        "
                                                        append-icon="mdi-calendar"
                                                        readonly
                                                        v-bind="attrs"
                                                        v-on="on"
                                                    />
                                                </template>
                                                <v-date-picker
                                                    v-model="
                                                        datepickerDateRange
                                                    "
                                                    :color="
                                                        getAppColors.primary
                                                    "
                                                    range
                                                    @change="
                                                        search = '';
                                                        initialize();
                                                    "
                                                />
                                            </v-menu>
                                            <v-select
                                                v-model="quickDateRange"
                                                :color="getAppColors.primary"
                                                :items="
                                                    getBlockchypDateRangeChoices
                                                "
                                                item-color="green"
                                                single-line
                                                hide-details
                                                return-object
                                                @change="initialize"
                                                v-else
                                            />
                                        </v-col>
                                        <v-col cols="6">
                                            <v-switch
                                                v-model="useCustomDateRange"
                                                color="green"
                                                label="Custom Date Range"
                                            />
                                        </v-col>
                                        <v-col cols="6">
                                            <v-switch
                                                v-model="showDeclines"
                                                color="orange"
                                                label="Show Declines"
                                            />
                                        </v-col>
                                    </v-row>
                                </v-card-text>
                            </v-card>
                        </v-menu>
                    </v-toolbar>
                </template>
                <template v-slot:no-data>
                    <v-alert prominent text type="info" class="ma-2">
                        <v-row align="center">
                            <v-col class="grow"> No records found</v-col>
                            <v-col class="shrink">
                                <v-btn
                                    color="green"
                                    class="my-2 white--text"
                                    @click="initialize"
                                >
                                    Retry
                                </v-btn>
                            </v-col>
                        </v-row>
                    </v-alert>
                </template>
            </v-data-table>
        </v-col>
    </v-row>
</template>

<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
import accounting from 'accounting';
import moment from 'moment';
import BoolStatus from '@/components/BoolStatus';

export default {
    name: 'TransactionList',
    components: { BoolStatus },
    data() {
        return {
            datePickerMenu: false,
            loading: false,
            alert: false,
            alertMessage: '',
            alertType: 'success',
            search: '',
            filterMenu: false,
            filterTypes: ['Charge', 'Refund', 'Reverse', 'Capture'],
            transactionTypes: [
                'Charge',
                'Refund',
                'Reverse',
                'Void',
                'Preauth',
                'Capture',
                'Enroll',
            ],
            tableFooterProps: {
                showFirstLastPage: true,
            },
            headers: [
                { text: 'Date', align: 'left', value: 'timestamp' },
                {
                    text: 'Sale ID',
                    align: 'center',
                    value: 'sale_id',
                },
                {
                    text: 'Transaction ID',
                    align: 'left',
                    value: 'transactionId',
                },
                {
                    text: 'Approved',
                    align: 'center',
                    value: 'approved',
                    filter: (value, search, item) => {
                        if (this.showDeclines) return true;

                        return item.approved;
                    },
                },
                { text: 'Card Holder', align: 'left', value: 'cardHolder' },
                { text: 'Amount', align: 'right', value: 'authorizedAmount' },
                {
                    text: 'Transaction Type',
                    align: 'center',
                    value: 'transactionType',
                    filter: (value, search, item) => {
                        if (!this.filterTypes.length) return true;
                        if (!item.transactionType) return false;

                        const filterTypesToUpperCase = this.filterTypes.map(
                            (filterType) => filterType.toUpperCase()
                        );

                        return filterTypesToUpperCase.includes(
                            item.transactionType.toUpperCase()
                        );
                    },
                },
                { text: 'Entry Method', align: 'left', value: 'entryMethod' },
                { text: 'Payment Type', align: 'left', value: 'paymentType' },
                { text: 'Masked Card', align: 'left', value: 'maskedPan' },
                {
                    text: 'Actions',
                    align: 'left',
                    value: 'actions',
                    sortable: false,
                    filterable: false,
                },
            ],
            transactions: [],
            useCustomDateRange: false,
            datepickerDateRange: [],
            quickDateRange: {
                text: 'Yesterday',
                value: 'yesterday',
                useForBlockchyp: true,
                days: null,
            },
            voidRefundDialog: false,
            captureDialog: false,
            voidOrRefund: 'void',
            voidRefundPartialAmountDialog: false,
            amount: '',
            selectedTransactionId: '',
            loadingActions: false,
            showDeclines: false,
            axiosSource: null,
        };
    },
    watch: {
        useCustomDateRange: function (newValue) {
            if (!newValue) {
                this.datepickerDateRange = [];
                this.initialize();
            }
        },
    },
    computed: {
        ...mapGetters([
            'getAppColors',
            'getInternalCoreClearController',
            'getInternalEmployeePreferencesController',
            'getBlockchypDateRangeChoices',
        ]),
        dateRangeText() {
            return this.datepickerDateRange
                .map((date) => moment(date).format('MM/DD/YYYY'))
                .join(' through ');
        },
        getTotal() {
            const filterTypesToUpperCase = this.filterTypes.map((filterType) =>
                filterType.toUpperCase()
            );

            let totalAmount = 0;
            this.transactions.forEach((transaction) => {
                if (
                    (!this.filterTypes.length ||
                        filterTypesToUpperCase.includes(
                            transaction.transactionType.toUpperCase()
                        )) &&
                    (this.showDeclines || transaction.approved)
                ) {
                    if (
                        transaction.transactionType === 'refund' ||
                        transaction.transactionType === 'void'
                    ) {
                        totalAmount -= transaction.approved
                            ? Number(
                                  transaction.authorizedAmount.replace(/,/g, '')
                              )
                            : 0;
                    } else {
                        totalAmount += transaction.approved
                            ? Number(
                                  transaction.authorizedAmount.replace(/,/g, '')
                              )
                            : 0;
                    }
                }
            });

            return accounting.formatMoney(totalAmount);
        },
        dateRange() {
            let daysFromStart = null;
            let daysFromEnd = null;

            let quickStartDate = null;
            let quickEndDate = null;

            if (this.quickDateRange !== {}) {
                switch (this.quickDateRange.value) {
                    case 'today':
                        daysFromStart = 0;
                        daysFromEnd = 0;
                        break;
                    case 'yesterday':
                        daysFromStart = 1;
                        daysFromEnd = 1;
                        break;
                    default:
                        daysFromStart = this.quickDateRange.days;
                        daysFromEnd = 0;
                }

                quickStartDate = moment().subtract(daysFromStart, 'days');
                quickEndDate = moment().subtract(daysFromEnd, 'days');
            }

            let startDate =
                this.datepickerDateRange[0] !== undefined
                    ? this.datepickerDateRange[0]
                    : quickStartDate !== null
                    ? quickStartDate
                    : '';
            let endDate =
                this.datepickerDateRange[1] !== undefined
                    ? this.datepickerDateRange[1]
                    : quickEndDate !== null
                    ? quickEndDate
                    : '';

            if (startDate !== '') {
                startDate = moment(startDate)
                    .set({
                        hour: 0,
                        minute: 0,
                        second: 0,
                        millisecond: 0,
                    })
                    .toISOString();
            }

            if (endDate !== '') {
                endDate = moment(endDate)
                    .set({
                        hour: 23,
                        minute: 59,
                        second: 59,
                        millisecond: 999,
                    })
                    .toISOString();
            }

            return { startDate: startDate, endDate: endDate };
        },
        dateRangeDisplay() {
            return this.dateRangeText
                ? this.dateRangeText
                : this.quickDateRange.text;
        },
    },
    mounted() {
        this.getFilterPreferences();
        this.initialize();
    },
    methods: {
        openAlert(type, message) {
            this.alertType = type;
            this.alertMessage = message;
            this.alert = true;
        },
        async initialize() {
            this.loading = true;
            this.datePickerMenu = false;

            try {
                if (this.axiosSource) this.axiosSource.cancel();
                this.axiosSource = axios.CancelToken.source();

                let response = await axios.get(
                    `${
                        this.getInternalCoreClearController +
                        '/get_transactions'
                    }`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {
                            start_date: this.dateRange.startDate,
                            end_date: this.dateRange.endDate,
                            search: this.search,
                        },
                        cancelToken: this.axiosSource.token,
                    }
                );

                this.loading = false;
                this.axiosSource = null;

                if (response.data.transactions) {
                    this.transactions = response.data.transactions;
                } else {
                    console.log('No transactions found');
                    this.transactions = [];
                }
            } catch (error) {
                console.error('Could not get list of transactions');
                console.error(error);

                this.openAlert('error', error.toString());

                this.loading = false;
                this.axiosSource = null;
                this.transactions = [];
            }
        },
        formatDateTime(rawDateTime) {
            return rawDateTime
                ? moment(rawDateTime).format('MM/DD/YYYY LT')
                : '';
        },
        formatDate(rawDateTime) {
            return rawDateTime ? moment(rawDateTime).format('MM/DD/YYYY') : '';
        },
        showVoidRefundDialog(voidOrRefund, transactionId) {
            this.voidOrRefund = voidOrRefund;
            this.voidRefundDialog = true;
            this.selectedTransactionId = transactionId;
        },
        async voidRefundTransaction(fullAmount = true) {
            if (fullAmount) {
                this.amount = '';
            }

            this.alert = false;
            this.voidRefundDialog = false;
            this.voidRefundPartialAmountDialog = false;
            this.loadingActions = true;

            let formData = new FormData();
            formData.append('amount', this.amount);
            formData.append('void_or_refund', this.voidOrRefund);

            try {
                let response = await axios.post(
                    `${
                        this.getInternalCoreClearController +
                        '/void_return_for_portal/' +
                        this.selectedTransactionId
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    }
                );

                this.loadingActions = false;

                if (response.data.success === true) {
                    this.openAlert('success', response.data.message);
                } else {
                    this.openAlert('error', response.data.message);
                }
            } catch (error) {
                this.voidRefundDialog = false;
                this.voidRefundPartialAmountDialog = false;

                console.error(error);
                this.openAlert('error', error);
            }
        },
        async capturePreauth() {
            this.alert = false;
            this.captureDialog = false;
            this.loadingActions = true;

            let formData = new FormData();

            try {
                let response = await axios.post(
                    `${
                        this.getInternalCoreClearController +
                        '/capture_preauth/' +
                        this.selectedTransactionId
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    }
                );

                this.loadingActions = false;

                if (response.data.success === true) {
                    let alertType = 'error';
                    let alertMessage = 'Unknown error';

                    if (response.data.approved === true) {
                        alertType = 'success';
                        alertMessage =
                            'Successfully captured transaction ' +
                            this.selectedTransactionId;
                    }

                    this.openAlert(alertType, alertMessage);
                } else {
                    this.openAlert('error', response.data.responseDescription);
                }
            } catch (error) {
                this.voidRefundDialog = false;
                this.voidRefundPartialAmountDialog = false;

                console.error(error);
                this.openAlert('error', error);
            }
        },
        async saveFilterPreferences() {
            let formData = new FormData();
            formData.append(
                'transaction_filters',
                JSON.stringify(this.filterTypes)
            );

            try {
                let response = await axios.post(
                    `${
                        this.getInternalEmployeePreferencesController +
                        '/save_transaction_filters'
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    }
                );

                if (response.status === 200) {
                    console.log('Filter preferences updated');
                } else {
                    console.log('Unable to save filter preferences');
                }
            } catch (error) {
                console.error(error);
            }
        },
        async getFilterPreferences() {
            try {
                let response = await axios.get(
                    `${
                        this.getInternalEmployeePreferencesController +
                        '/get_transaction_filters'
                    }`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {},
                    }
                );

                if (response.data) {
                    this.filterTypes = response.data;
                } else {
                    console.log('No filter preferences found');
                    this.filterTypes = ['Charge', 'Refund', 'Capture'];
                }
            } catch (error) {
                console.error('Could not get filter preferences');
                console.error(error);
            }
        },
        excelExport() {
            const lowercasedFilterTypes = this.filterTypes.map((filterType) =>
                filterType.toLowerCase()
            );

            const showDeclines = this.showDeclines ? 1 : 0;
            window.location.href =
                this.getInternalCoreClearController +
                '/excel_export_transactions_for_portal?start_date=' +
                this.dateRange.startDate +
                '&end_date=' +
                this.dateRange.endDate +
                '&search=' +
                this.search +
                '&transaction_type=' +
                encodeURIComponent(JSON.stringify(lowercasedFilterTypes)) +
                '&show_declines=' +
                showDeclines;
        },
    },
    filters: {
        titleCase: (value) => {
            if (!value) return '';

            value = value.toString().toLowerCase().split(' ');

            for (let i = 0; i < value.length; i++) {
                value[i] = value[i][0].toUpperCase() + value[i].slice(1);
            }

            return value.join(' ');
        },
        currency: (value, transactionType) => {
            if (!value) return '0';

            let formattedValue = accounting.formatMoney(value);

            return transactionType === 'void' ||
                transactionType === 'refund' ||
                transactionType === 'reverse'
                ? '-' + formattedValue
                : formattedValue;
        },
    },
};
</script>
